<template>
    <div class="blog">
        <div class="header">
            <div :class="{ active: isworld }" @click="topath('world')">
                世界
            </div>
            <div :class="{ active: iswb }" @click="topath('wb')">微博热搜</div>
            <div :class="{ active: iszh }" @click="topath('zhihu')">知乎</div>
            <div :class="{ active: isthree }" @click="topath('three')">360</div>
            <div :class="{ active: isdy }" @click="topath('dy')">抖音</div>
            <div :class="{ active: isbl }" @click="topath('bl')">哔哩哔哩</div>
        </div>
        <!-- 其他 -->
        <div v-for="(item, key) in data" :key="item.title" v-show="isurl">
            {{ key + 1 }}、<a :href="item.url">{{ item.title }}</a>
        </div>
        <!-- 世界60s -->
        <div v-for="(item, key) in data2" :key="key" v-show="!isurl">
            {{ key + 1 }}、{{ item }}
        </div>
    </div>
</template>

<script>
export default {
    name: "blog",
    data() {
        return {
            data: [],
            data2: [],
            isurl: false,
            // 当前展示
            isworld: true,
            iswb: false,
            iszh: false,
            isthree: false,
            isdy: false,
            isbl: false,
        };
    },
    components: {},
    methods: {
        topath(path) {
            switch (path) {
                case "world":
                    // 韩小韩 60s
                    this.$axios("https://api.vvhan.com/api/60s?type=json").then(
                        (res) => {
                            this.data2 = res.data.data;
                            this.isurl = false;
                            // 更换显示
                            this.isworld = true;
                            this.iswb =
                                this.iszh =
                                this.isthree =
                                this.isdy =
                                this.isbl =
                                    false;
                        }
                    );
                    break;
                case "wb":
                    // 韩小韩 微博热搜实时API接口
                    this.$axios({
                        method: "get",
                        url: "https://api.vvhan.com/api/wbhot",
                    }).then((res) => {
                        this.data = res.data.data;
                        this.isurl = true;
                        // 更换显示
                        this.iswb = true;
                        this.isworld =
                            this.iszh =
                            this.isthree =
                            this.isdy =
                            this.isbl =
                                false;
                    });
                    break;
                case "zhihu":
                    // 故梦 - 知乎热搜排行榜
                    // https://api.gumengya.com/Docs/#/Doc/MD/Api/ZhiHuHot
                    this.$axios({
                        method: "get",
                        url: "https://api.gumengya.com/Api/ZhiHuHot?format=json",
                    }).then((res) => {
                        this.isurl = true;
                        this.data = res.data.data;
                        // 更换显示
                        this.iszh = true;
                        this.isworld =
                            this.iswb =
                            this.isthree =
                            this.isdy =
                            this.isbl =
                                false;
                    });
                    break;
                case "dy":
                    // 故梦 - 抖音热搜排行榜
                    this.$axios({
                        method: "get",
                        url: "https://api.gumengya.com/Api/DouYinHot?format=json",
                    }).then((res) => {
                        this.isurl = true;
                        this.data = res.data.data;
                        // 更换显示
                        this.isdy = true;
                        this.iswb =
                            this.isworld =
                            this.iszh =
                            this.isthree =
                            this.isbl =
                                false;
                    });
                    break;
                case "three":
                    // 故梦 - 360
                    this.$axios({
                        method: "get",
                        url: "https://api.gumengya.com/Api/SoHot?format=json",
                    }).then((res) => {
                        this.isurl = true;
                        this.data = res.data.data;
                        // 更换显示
                        this.isthree = true;
                        this.isworld =
                            this.iswb =
                            this.iszh =
                            this.isdy =
                            this.isbl =
                                false;
                    });
                    break;
                case "bl":
                    // 故梦 - bili
                    this.$axios({
                        method: "get",
                        url: "https://api.gumengya.com/Api/BiliBliHot?format=json",
                    }).then((res) => {
                        this.isurl = true;
                        this.data = res.data.data;
                        // 更换显示
                        this.isbl = true;
                        this.isworld =
                            this.iswb =
                            this.iszh =
                            this.isthree =
                            this.isdy =
                                false;
                    });
                    break;
            }
        },
    },
    mounted() {
        // 韩小韩 60s
        this.$axios("https://api.vvhan.com/api/60s?type=json").then((res) => {
            this.data2 = res.data.data;
        });
    },
};
</script>

<style lang="less" scoped>
.blog {
    width: 100%;
    height: 100%;
    .header {
        display: flex;
        max-width: 300px;
        justify-content: space-around;
        margin: 0 auto;
        div {
            cursor: pointer;
            white-space: nowrap;
        }
        div:hover {
            color: orange;
        }
        .active {
            color: orange;
        }
    }
    div {
        a {
            color: black;
            text-decoration: none;
        }
        a:hover {
            color: orange;
        }
    }
}
</style>